.nav-bar {
  background-color: $clr1;
  height: 250px;
  width: 100%;
}

.nav-bar-subcontainer {
  padding-top: 73px;
  height: 100%;
}

.logo-menu-container {
  padding-top: 4px;
}

.logo-container {
  width: 160px;
  height: 40px;
  margin-right: 80px;
}

.menu-btn {
  display: none;
}

.home-button {
  padding-top: 10px;
  margin-right: 43px;
}

.about-button {
  padding-top: 10px;
}

.home-button:hover,
.about-button:hover {
  color: $clr4;
}

.full-menu {
  display: flex;
  flex: 1;
  justify-content: space-between;
}

@include for-tablet-layout {
  .nav-bar {
    height: 176px;
  }

  .nav-bar-subcontainer {
    padding-top: 64px;
  }

  .logo-menu-container {
    padding-top: 8px;
  }

  .logo-container {
    width: 128px;
    height: 32px;
    margin-right: 48px;
    img {
      height: 100%;
    }
  }
}

@include for-mobile-layout {
  .nav-bar {
    height: 128px;
  }

  .nav-bar-subcontainer {
    padding-top: 48px;
  }

  .logo-container {
    margin-right: 0;
  }

  .logo-menu-container {
    width: 100%;
    justify-content: space-between;
    padding-top: 0;
  }

  .menu-btn {
    display: flex;
    height: 17px;
    width: 20px;
    margin-top: 8px;
  }

  .full-menu {
    display: none;
  }
}
